<template>
  <div :class='`nav nav-${theme}`'>
    <div v-if='!isMobile' class='nav-side'>
      <router-link class='nav-opt' to='/'>home</router-link>
      <router-link class='nav-opt' to='/dex/ou'>battle dex</router-link>
      <router-link class='nav-opt' to='/battlepark'>battle park</router-link>
      <p class='nav-update'>latest update: 07/2020</p>
    </div>

    <input
      v-if='isMobile && tier && !pokemon'
      @keyup='emitSearch'
      v-model='search'
      class='nav-search'
      type='text'
      placeholder='Search...'
    />
    <select @change='changeTier' v-if='isMobile && tier && !pokemon' class='nav-select' name>
      <option v-for='(tier, key) in tiers' :key='key' :value='tier.path'>{{tier.path}}</option>
    </select>

    <div>
      <label class='nav-theme'>Theme:</label>
      <select @change='setTheme'>
        <option value='light'>Light</option>
        <option value='dark'>Dark</option>
      </select>
    </div>
  </div>
</template>

<script>
import EventBus from "@/event.bus";

export default {
  data() {
    return {
      search: "",
      tiers: [
        { tier: "Over Used", alias: "OU", path: "ou" },
        { tier: "Under Used", alias: "UU", path: "uu" },
        { tier: "Battle Stadium Singles", alias: "BSS", path: "bss" },
        { tier: "Ubers", alias: "UBERS", path: "ubers" },
        { tier: "Video Game Championship", alias: "VGC", path: "vgc" },
        { tier: "Rarely Used", alias: "RU", path: "ru" },
        { tier: "Monotype", alias: "Monotype", path: "monotype" },
        { tier: "Little Cup", alias: "LC", path: "lc" },
        { tier: "Not Fully Evolved", alias: "NFE", path: "nfe" }
      ]
    };
  },
  methods: {
    setTheme(e) {
      this.$store.commit("dex/SET_THEME", e.target.value);
    },
    emitSearch() {
      const word = this.search.toLowerCase();
      if (word === "") {
        EventBus.$emit("SEARCH-RESET");
        return;
      }

      let s = [];
      for (let pkmn in this.battledex) {
        if (word.length > this.battledex[pkmn].slug.length) continue;
        else if (this.battledex[pkmn].slug.startsWith(word)) {
          s.push(this.battledex[pkmn]);
        }
      }
      EventBus.$emit("SEARCHED", s);
    },
    changeTier(e) {
      const tier = e.target.value;
      EventBus.$emit("CHANGE-TIER", tier);
    }
  },
  computed: {
    tier() {
      return this.$route.params.tier;
    },
    pokemon() {
      return this.$route.params.pokemon;
    }
  }
};
</script>

